<template><section><html><head></head><body><h1>VcAnalysisFlood</h1>
<p>The <code v-pre="">vc-analytics-flood</code> component is used to simulate flood analysis. <strong>Note</strong> Scene loading terrain or 3DTiles is required for analysis.</p>
<h2>Example</h2>
<h3>Load a VcAnalysisFlood</h3>
<h4>Preview</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer @ready="ready">
        <vc-analytics-flood ref="flood" :min-height="minHeight" :max-height="maxHeight" :speed="speed" :polygon-hierarchy="polygonHierarchy" @stop="onStoped">
        </vc-analytics-flood>
        <vc-provider-terrain-cesium></vc-provider-terrain-cesium>
        <vc-layer-imagery>
          <vc-provider-imagery-bingmaps :url="url" :bm-key="bmKey" map-style="Aerial"></vc-provider-imagery-bingmaps>
        </vc-layer-imagery>
      </vc-viewer>
      <div class="demo-tool">
        <md-input-container>
          <label>minHeight</label>
          <md-input v-model.number="minHeight"></md-input>
        </md-input-container>
        <md-input-container>
          <label>maxHeight</label>
          <md-input v-model.number="maxHeight"></md-input>
        </md-input-container>
        <span>speed</span>
        <vue-slider v-model="speed" :min="1" :max="100" :interval="1"></vue-slider>
        <md-button class="md-raised md-accent" @click="start">Start</md-button>
        <md-button :disabled="!starting" class="md-raised md-accent" @click="pause">{{pausing ? 'Start' : 'Pause'}}</md-button>
        <md-button class="md-raised md-accent" @click="stop">Stop</md-button>
      </div>
    </div>
  </template>
  
